@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@500&display=swap);
*{box-sizing:border-box;font-family:'Fira Code'}body{background:#111;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.app{display:-webkit-flex;display:flex;height:100vh}.code-box{margin:auto;width:85%;max-width:900px;box-shadow:4px 4px rgba(204,204,204,0.266667)}.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;width:100%;color:white;position:absolute}.header .top-mid{text-align:center}.header .top-mid p{text-shadow:4px 4px rgba(204,204,204,0.266667)}.header span{margin:1rem auto}.footer{display:-webkit-flex;display:flex;width:100%;position:absolute;bottom:0;-webkit-justify-content:space-around;justify-content:space-around}.footer .status{color:white;opacity:0;font-weight:bold}.footer .status.pop{-webkit-animation:pop 1150ms ease-out;animation:pop 1150ms ease-out}@-webkit-keyframes pop{0%{opacity:1}100%{opacity:0}}@keyframes pop{0%{opacity:1}100%{opacity:0}}

